.project-heading {
  text-align: center;
  padding: 4rem 0 2rem 0;
}
.project-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  padding-bottom: 5rem;
}
.project-card {
    border: 1px solid #eee;
  background: #1a1919;
  padding: 1.2rem 1rem;
  border-radius: 8px; /* Rounded corners */
  transition: transform 0.3s; /* Smooth hover effect */
}
.project-card:hover {
  transform: scale(1.05); /* Scale effect on hover */
}
.pro-btns .btn {
  padding: 0.5rem 1rem;
  background: rgb(248, 217, 15);
  text-decoration: none;
  border-radius: 5px; /* Rounded button edges */
}

.pro-btns .btn:hover {
  background: rgba(255, 255, 255, 0.2);
}
.project-card img {
  width: 100%;
}
.project-title {
  color: #fff;
  padding: 1rem 0;
}
.pro-details p {
  padding-bottom: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}

.pro-btns {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}

.pro-btns .btn {
  padding: 0.5rem 1rem;
}

@media screen and (max-width: 740px) {
  .project-container {
    max-width: 90%;
    margin: auto;
    grid-template-columns: 1fr;
  }
}
